@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #f0f2f5;
  font-family: "Inter",sans-serif;
}

.card{
  background-color: #fff;
  width: 55%;
  min-width: 550px;
  padding: 50px;
  padding-bottom: 350px;
  position: absolute;
  transition: translate(-50%,-50%);
  top: 20%;
  left: 25%;
  right: 25%;
  bottom: 20%;
  border-radius: 8px;
}

h1{
  background: #fff;
  font-size: 30px!important;
  margin-bottom: 2rem;
  font-weight: 700;
  color: #333;
}

.currency_exchange{
  display: flex;
  justify-content: space-between;
  background-color: #fff;
}

.input_container{
  width: 30%;
  height: fit-content;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 10px;
  background-color: #fff;
}

.input_label{
  font-size: 0.95rem;
  color: #8B8E98;
  font-weight: 600;
  background: #fff;
}

.input_field{
  width: auto;
  height: 60px;
  background: #fff;
  padding: 0 0 0 40px;
  border-radius: 7px;
  outline: none;
  filter: drop-shadow(0px 1px 0px #efefef) drop-shadow(0px 1px 0.5px rgba(239,239,239,0.5));
  transition: all 0.3s cubic-bezier(0.15,0,83,0.66,1);
}

