.output{
  background: #ffffff;
  padding-bottom: 20px;
  width: 100%;
}

h2{
  width: 50%;
  margin-top: 20px;
  background-color: #e8f8fd;
  padding: 26px;
  border-radius: 10px;
  font-size: 15px;
}
